@import url("https://fonts.googleapis.com/css?family=Roboto&display=swap");
@import url("https://fonts.googleapis.com/css?family=Oswald&display=swap");

html {
  height: 100%;
}
body {
  display: flex;
  flex-direction: column;
  height: 100%;
  margin: 0;
  font-family: Roboto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root {
  flex: 1 0 auto;
  & p:last-child {
    margin-bottom: 2rem;
  }
  background: linear-gradient(
    138deg,
    rgb(26, 30, 36) 20%,
    rgb(40, 44, 52) 50%,
    rgb(26, 30, 36) 80%
  );
}

.recharts-responsive-container {
  margin: 3rem auto 1rem auto;
}
.recharts-default-tooltip {
  box-shadow: 0.1em 0.1em 1em 0em rgba(0, 0, 0, 0.5);
  /* transform: perspective(300px) rotateY(-2deg); */
}

.recharts-tooltip-item {
  margin-right: 1rem;
}

.recharts-surface {
  /* margin-left: -10%; */
  /* transform: perspective(300px) rotateY(-2deg); */
}
.recharts-legend-wrapper {
  /* transform: perspective(0px) rotateY(0deg); */
}
.recharts-legend-item-text,
.recharts-tooltip-item-name,
.recharts-surface {
  font-family: Oswald;
  text-transform: capitalize;
  transition: all 0.2s linear;
}

.recharts-default-legend li {
  cursor: pointer;
}
.recharts-brush {
  & > rect:first-child {
    fill: rgb(26, 30, 36);
    fill-opacity: 1;
  }
  & .recharts-brush-slide {
    fill: rgb(40, 44, 52);
    fill-opacity: 1;
  }
}

.recharts-reference-line text {
  fill: rgb(26, 30, 36);
  text-transform: none;
  font-style: italic;
  /* display: none; */
  & tspan {
    fill: white;
  }
}
@media screen and (min-width: 800px) {
  .recharts-reference-line text {
    display: block;
  }
  /* CSS styles for screens with a width greater than 800px */
}
a {
  color: white;
  font-family: Oswald;
}

h1,
h2 {
  color: #eeeeee;
  font-family: Oswald;
  padding: 0 1rem;
  margin: 1em auto;
}
p {
  line-height: 1.4rem;
  margin: 1em auto;
  max-width: 720px;
  color: white;
  padding: 0em 1rem;
  text-align: left;
}
footer {
  background-color: rgb(26, 30, 36);
  border-top: 1px solid rgb(40, 44, 52);
  & div {
    padding: 1rem;
    max-width: 480px;
    margin: auto;
    margin-bottom: 1rem;
    color: gray;
  }
}
.info {
  display: inline-block;
  color: #dddddd;
  font-style: italic;
  margin-top: 1.5rem;
}
.reset {
  background: none;
  font-size: 0.9rem;
  color: white;
  font-family: Roboto;
  display: inline-block;
  font-style: normal;
  cursor: pointer;
  margin-left: 0.25rem;
  padding: 0.25rem 0.5rem;
  border-radius: 0.5rem;
  border: 3px solid rgb(102, 102, 102);
  transition: all 0.2s linear;
  &:hover {
    border: 3px solid white;
  }
}
